<template>
  <nav class="nav boder4 fix">
    <!-- Brand and toggle get grouped for better mobile display -->
    <div class="titleBox none">
      <p class="bold16" style="margin-top: 2px;"> {{title}}</p>
    </div>
    <!-- Collect the nav links, forms, and other content for toggling -->
    <ul class="nav nav-tabs">
      <li role="presentation"  v-for="lis in tabList" @click='getExperts(lis.itemKey)' :class="{'active':lis.check}"><a href="javascript:;">{{lis.text}}</a></li>
    </ul>
  </nav>
</template>
<script>
   import bus from '../../common/common.js';
    export default {
        name: "my-nav",
        created(){
            this.getList();
        },
        data(){
            return  {
                 "title":"专家在线",
                tabList:[],
                Authorization:'',
              tabList:[
                {text:"全部",itemKey:"expect_group",check:true},
                {text:"心电生理",itemKey:"1",check:false},
                {text:"冠脉血管",itemKey:" ",check:false},
                {text:"高血压",itemKey:"2",check:false},
                {text:"心力衰竭",itemKey:"3",check:false}
              ],
            }
        },
        methods: {
             getExperts(itemKey){
                  for( var i in this.tabList){
                    console.log(this.tabList[i].itemKey);
                    console.log(itemKey);
                    if(itemKey == this.tabList[i].itemKey){
                      this.tabList[i].check=true;
                    }else{
                      this.tabList[i].check=false;
                    }
                  }
             },
            // tab列表
            getList(){
                var url='/api/basic/basicDicts/expect_group/list';
                 this.Authorization=localStorage.getItem('access_token')
                // console.log(Authorization)
                this.$http.get(url, {
                    headers: {
          Authorization: 'bearer '+ this.Authorization,
                    }
                }).then(function(res){
                    this.tabList=res.body
                    res.body.unshift({itemValue:'全部',itemKey:'expect_group'})
                })
            },
            getExperts(itemKey){
                 var url='/api/organization/employees/'+itemKey+'/list';
                 this.$http.get(url,{
                    headers:{
                         Authorization: 'bearer '+ this.Authorization,
                    }
                 }).then(function(res){
                    //  console.log(res);
                    bus.$emit('employees',res.body);

                 })
            }
        },
        computed: {
            ChildByNavToList: function () {

            }
        }
    }
</script>



