<script>
import CommonHeader from '../public/header.vue';
import Set from '../public/set.vue';
import NavTop1 from '../public/nva1.vue';
import NavTop from '../public/navTop.vue';
import moment from 'moment'
// import ExpertList from '../experts/expertList.vue';
export default {
  name: "Expert",
  data: function() {
    return {
      Authorization: '',
      bankCardsMes: {},
      noBinging: false,
      okBinging: false,
      accountBalance: {},
      iconShow: '',
      cashWithdrawal: {},
      variousIncome: {},
      progressBarIf: false,
      flag: false,
      errMes: ''
    }
  },
  created() {
    // 获取用户银行卡信息
    this.getBankCardMes()
    // 获取账户余额
    this.getaccountBalance();
    // 提现中金额
    this.getCashWithdrawal();
    // 查询总协诊数量和收入、图文总收入、视频总收入
    this.checkNumberImg()
  },
  methods: {
    cardNumberFilter(cardNumber) {
      var Top4 = cardNumber.slice(0, 4)
      var After4 = cardNumber.slice(cardNumber.length - 4, cardNumber.length);
      var num = Top4 + ' ' + '****' + ' ' + '****' + ' ' + After4
      return num
    },
    success() {
      this.$message({
        message: '保存成功',
        type: 'success'
      });
    },
    warning() {
      this.$message({
        message: '保存失败',
        type: 'warning'
      });
    },
    warning2() {
      this.$message({
        message: '对不起，大于1000才能提现',
        type: 'warning'
      });
    },
    warning3() {
      this.$message({
        message: '您还没有绑定银行卡',
        type: 'warning'
      });
    },
    goBank() {
      this.$router.push({ name: 'BindBank' })
    },
    goIncomeDetails() {
      this.$router.push({ name: 'IncomeDetails' })
    },
    // 查看用户银行卡信息
    getBankCardMes() {
      this.Authorization = localStorage.getItem('access_token');
      var url = '/api/account/bankCards/self';
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        },

      }).then(function(res) {
        this.bankCardsMes = res.body;
        this.noBinging = false;
        this.okBinging = true

      }, function(err) {
        if (err.body.status == 500 && err.body.code == 200) {
          this.noBinging = true;
          this.okBinging = false;
          this.flag = true;
          this.errMes = err.body.message
        }
      })
    },
    // 获取用户的银行卡余额
    getaccountBalance() {
      this.Authorization = localStorage.getItem('access_token');
      var url = '/api/assist-order/orderDetail/self';
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        this.accountBalance = res.body;
      })
    },
    // 查看提现中金额
    getCashWithdrawal() {
      this.Authorization = localStorage.getItem('access_token');
      var url = '/api/assist-order/orderDetail/cash';
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        this.cashWithdrawal = res.body
        if (this.cashWithdrawal.money == null) {
          this.progressBarIf = false
        } else {
          this.progressBarIf = true
          this.cashWithdrawal.cashWithdrawal = moment(this.cashWithdrawal.cashWithdrawal).format('YYYY-MM-DD hh:mm:ss')
        }
      })
    },
    //  金额提现
    Withdrawals() {
      // if (this.flag) {
      //   this.$message({
      //     message: this.errMes,
      //     type: 'warning'
      //   });
      //   this.goBank()
      //   return
      // }
      // if (this.cashWithdrawal.withdrawCash > 0) {
      //   this.$message({
      //     message: '有笔提现正在进行，请先等待该笔提现完成',
      //     type: 'warning'
      //   });
      //   return
      // }
      // if (this.accountBalance.balance < 1000) {
      //   this.warning2();
      //   return
      // }
      var url = '/api/assist-order/orderDetail/access'
      this.Authorization = localStorage.getItem('access_token');
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(res => {
        if (!res.body.access) {
          this.$message({
            message: res.body.message,
            type:'warning'
          })
        }
        if (res.body.access) {
          this.$router.push({ name: 'Withdrawals' })
        }
        if (res.body.access == false && res.body.redirect == true) {
          this.goBank();
        } 


      }, err => {
        this.$message({
          message: '网络连接失败',
          type: 'warning'
        })

      })

    },
    //    查询总协诊数量和收入、图文总收入、视频总收入
    checkNumberImg() {
      this.Authorization = localStorage.getItem('access_token');
      var url = '/api/assist-order/orderDetail/calc'
      this.$http.get(url, {
        headers: {
          Authorization: 'bearer ' + this.Authorization
        }
      }).then(function(res) {
        this.variousIncome = res.body
      })
    }
  },
  components: {
    CommonHeader: CommonHeader, Set: Set, NavTop1: NavTop1, NavTop: NavTop
  },
}
